
.title {
  font-size: 20px;
  color: #333;
  text-align: center;
  margin: 32px 0;
}

.box {
  background-color: #fafafa;
  border-radius: 4px;
  border: 1px solid #fff;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  margin: 0 auto 16px auto;
  max-width: 720px;

  padding: 0 16px 16px 16px;
  width: calc(100% - 32px);

  h4 {
    padding-top: 16px;
  }

  p {
    line-height: 32px;
  }

  .links {
    margin-top: 16px;

    a {
      color: #369;
    }

    span {
      color: #eee;
      margin: 0 16px;
    }
  }
}
